<template>
    <div class="myHeader">
        <el-form :inline="true">
            <el-form-item>
                <el-button
                    text
                    @click="handler"
                    :style="{
				transform: menuToggle ? 'rotate(0)' : 'rotate(180deg)',
			}"
                >
                    <el-icon>
                        <Expand />
                    </el-icon>
                </el-button>
            </el-form-item>
            <el-form-item>
                <el-input v-model="input" placeholder="请输入内容" width></el-input>
            </el-form-item>
        </el-form>
        <div style="display: flex;align-items: center">
            <span style="display: flex;align-items: center;font-size:15px;cursor: default">
                <i class="ri-music-line" style="margin:2px 5px 0 0"></i>
                {{nowLyric}}
            </span>
            <el-switch
                v-model="isDark"
                class="mt-2"
                style="margin-left: 24px"
                inline-prompt
                :active-icon="Moon"
                :inactive-icon="Sunny"
            />
        </div>
    </div>
</template>

<script setup>
import { Sunny, Moon } from "@element-plus/icons-vue";
import { computed, ref, watch } from "vue";
import { useStore } from "vuex";

const store = useStore();

const nowLyric = computed(() => {
    return store.state.nowLyric;
});

const menuToggle = computed(() => {
    return store.state.menuToggle;
});
const handler = () => {
    store.commit("menuToggle");
};

const isDark = ref(false);
watch(
    () => store.state.isDark,
    val => {
        isDark.value = val;
    },
    {
        immediate: true
    }
);

watch(isDark, val => {
    store.commit("useDark", val);
});
</script>

<style lang="less" scoped>
.myHeader {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: space-between;
    .el-form {
        height: 32px;
        .el-button {
            font-size: 22px;
        }
    }
}
</style>
