<template>
    <div>
        <div class="player blur" :style="{left: flag ? '83px':'210px'}">
            <div class="player_left">
                <el-image
                    :src="pic + '?param=80y80'"
                    v-loading="audioLoading"
                    element-loading-background="rgba(0, 0, 0, 0.5)"
                >
                    <template #error>
                        <div class="image-slot">
                            <el-icon>
                                <Picture />
                            </el-icon>
                        </div>
                    </template>
                </el-image>
            </div>
            <div class="player_right">
                <div class="player_right_top">
                    <div class="player_info">
                        <h3>{{ nowPlay.song }}</h3>
                        <p>{{ nowPlay.singer }}</p>
                    </div>
                    <ul class="player_center_btn" style="flex:2;justify-content:center">
                        <li @click="backBtn">
                            <i class="ri-skip-back-line"></i>
                        </li>
                        <li @click="playBtn">
                            <i :class="playBtnIcon"></i>
                        </li>
                        <li @click="nextBtn">
                            <i class="ri-skip-forward-line"></i>
                        </li>
                    </ul>
                    <ul class="player_right_btn" style="flex:1;justify-content:end">
                        <li style="font-size: 28px" v-if="store.state.nowPlay.mv" @click="openMV">
                            <i class="ri-youtube-line"></i>
                        </li>
                        <li style="font-size: 28px" @click="playlistToggle">
                            <i class="ri-play-list-line"></i>
                        </li>
                    </ul>
                    <div></div>
                </div>
                <div class="player_right_bottom">
                    <el-slider
                        v-model="audioLineVal"
                        size="small"
                        :format-tooltip="formatTooltip"
                        @change="audioLineChange"
                    />
                </div>
            </div>
        </div>
        <Playlist />
        <audio id="Audio" :src="url" autoplay style="display:none" />
    </div>
</template>
<script setup>
import Playlist from "@/components/playlist/Index.vue";
import { ref, computed, onMounted, watch } from "vue";
import * as axios from "axios";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
const store = useStore();
const router = useRouter();

const pic = ref("");
const playBtnIcon = ref("ri-pause-circle-line");
const audioLoading = ref(false);
const audioLineVal = ref(0);
const audioNowTime = ref("00:00");
const audioAllTime = ref("00:00");
const lyric = ref(null);
const lineNum = ref(null);
const preLine = ref(2);

const flag = computed(() => {
    return store.state.menuToggle;
});

const playlistToggle = () => {
    store.commit("playlistToggle");
};

const nowPlay = computed(() => {
    return store.state.nowPlay;
});

const url = computed(() => {
    return `https://music.163.com/song/media/outer/url?id=${nowPlay.value.id}`;
});

const playBtn = () => {
    let audio = document.querySelector("#Audio");
    audio.paused ? audio.play() : audio.pause();
};

const backBtn = () => {
    store.commit("setNowIndex", store.state.nowIndex - 1);
};
const nextBtn = () => {
    store.commit("setNowIndex", store.state.nowIndex + 1);
};

const openMV = () => {
    document.querySelector("#Audio").pause();
    router.push({
        path: "/mv",
        query: { id: store.state.nowPlay.id, mv: store.state.nowPlay.mv }
    });
};

const timeFormat = val => {
    let minute = Math.floor(val / 60);
    minute = minute.toString().length === 1 ? "0" + minute : minute;
    let second = Math.round(val % 60);
    second = second.toString().length === 1 ? "0" + second : second;
    return `${minute}:${second}`;
};

const audioLine = () => {
    let audio = document.querySelector("#Audio");
    let str =
        timeFormat(audio.duration) == "NaN:NaN"
            ? "00:00"
            : timeFormat(audio.duration);
    audioNowTime.value = timeFormat(audio.currentTime);
    audioAllTime.value = str;
    audioLineVal.value = (audio.currentTime / audio.duration.toFixed(3)) * 100;
};

const formatTooltip = val => {
    return `${audioNowTime.value} / ${audioAllTime.value}`;
};
const audioLineChange = val => {
    let audio = document.querySelector("#Audio");
    audio.currentTime = val * 0.01 * audio.duration;
    audio.play();
};

const getLyric = () => {
    store.commit("setNowLyric", "歌词加载中");
    lyric.value = null;
    axios
        .get(`${store.state.webService}/lyric?id=${store.state.nowPlay.id}`)
        .then(res => {
            if (res.data.nolyric) {
                store.commit("setNowLyric", "该歌曲为纯音乐或暂无歌词");
            } else {
                createLrcObj(res.data.lrc.lyric);
            }
        });
};
const createLrcObj = lrc => {
    let oLRC = {
        ti: "",
        ar: "",
        al: "",
        by: "",
        offset: 0,
        ms: []
    };
    let lrcs = lrc.split("\n");
    for (let i in lrcs) {
        lrcs[i] = lrcs[i].replace(/(^\s*)|(\s*$)/g, "");
        let t = lrcs[i].substring(
            lrcs[i].indexOf("[") + 1,
            lrcs[i].indexOf("]")
        );
        let s = t.split(":");
        if (isNaN(parseInt(s[0]))) {
            for (let i in oLRC) {
                if (i != "ms" && i == s[0].toLowerCase()) {
                    oLRC[i] = s[1];
                }
            }
        } else {
            let arr = lrcs[i].match(/\[(\d+:.+?)\]/g);
            let start = 0;
            for (let k in arr) {
                start += arr[k].length;
            }
            let content = lrcs[i].substring(start);
            for (let k in arr) {
                let t = arr[k].substring(1, arr[k].length - 1);
                let s = t.split(":");
                oLRC.ms.push({
                    t: (parseFloat(s[0]) * 60 + parseFloat(s[1])).toFixed(3),
                    c: content
                });
            }
        }
    }
    oLRC.ms.sort(function(a, b) {
        return a.t - b.t;
    });
    lyric.value = oLRC.ms;
};

onMounted(() => {
    store.commit("setNowIndex", 0);
    let audio = document.querySelector("#Audio");
    audio.onpause = () => {
        playBtnIcon.value = "ri-play-circle-line";
    };
    audio.onplay = () => {
        playBtnIcon.value = "ri-pause-circle-line";
    };
    audio.onloadstart = () => {
        audioLoading.value = true;
        getLyric();
    };
    audio.oncanplay = () => {
        audioLoading.value = false;
    };
    audio.ontimeupdate = () => {
        audioLine();
        for (let i in lyric.value) {
            if (audio.currentTime > lyric.value[i].t) {
                lineNum.value = i;
            }
        }
    };
});

watch(
    router.currentRoute,
    val => {
        if (val.path.includes("mv")) {
            document.querySelector("#Audio").pause();
            playBtnIcon.value = "ri-play-circle-line";
        }
    },
    {
        deep: true,
        immediate: true
    }
);

watch(
    nowPlay,
    async val => {
        if (!val.id) return false;
        Promise.any([
            `https://p1.music.126.net/${val.pic}.jpg`,
            `https://p2.music.126.net/${val.pic}.jpg`,
            `https://p3.music.126.net/${val.pic}.jpg`,
            `https://p4.music.126.net/${val.pic}.jpg`
        ]).then(res => {
            pic.value = res;
        });
    },
    {
        immediate: true
    }
);
watch(lineNum, val => {
    store.commit("setNowLyric", lyric.value[val].c);
    if ("mediaSession" in navigator) {
        navigator.mediaSession.metadata = new MediaMetadata({
            title: `${store.state.nowPlay.song} - ${store.state.nowPlay.singer}`,
            artist: `${store.state.nowLyric}`,
            album: val.album,
            artwork: [
                {
                    src: `${pic.value}?param=96y96`,
                    sizes: "96x96",
                    type: "image/jpg"
                },
                {
                    src: `${pic.value}?param=128y128`,
                    sizes: "128x128",
                    type: "image/jpg"
                },
                {
                    src: `${pic.value}?param=192y192`,
                    sizes: "192x192",
                    type: "image/jpg"
                },
                {
                    src: `${pic.value}?param=256y256`,
                    sizes: "256x256",
                    type: "image/jpg"
                },
                {
                    src: `${pic.value}?param=384y384`,
                    sizes: "384x384",
                    type: "image/jpg"
                },
                {
                    src: `${pic.value}?param=512y512`,
                    sizes: "512x512",
                    type: "image/jpg"
                }
            ]
        });
        navigator.mediaSession.setActionHandler("previoustrack", () =>
            backBtn()
        );
        navigator.mediaSession.setActionHandler("nexttrack", () => nextBtn());
    }
});
</script>

<style lang="less" scoped>
.player {
    box-shadow: 0 0 15px #aaa;
    position: fixed;
    left: 210px;
    right: 20px;
    bottom: 20px;
    height: 100px;
    border-radius: 8px;
    z-index: 3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 14px;
    box-sizing: border-box;
    transition: 0.3s;
    display: flex;
    flex: 1;
    min-width: 430px;
    @media (max-width: 950px) {
        left: 20px !important;
    }
    .player_left {
        display: flex;
        flex-direction: column;
        position: relative;
        .el-image {
            width: 80px;
            height: 80px;
            border-radius: 5px;
            .image-slot {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 100%;
                height: 100%;
                background: var(--el-fill-color-light);
                color: var(--el-text-color-secondary);
                font-size: 30px;
                .el-icon {
                    font-size: 30px;
                }
            }
        }
    }
    .player_right {
        display: flex;
        flex: 1;
        flex-direction: column;
        padding-left: 10px;
        .player_right_top {
            display: flex;
            flex: 1;
            flex-direction: row;
            .player_info {
                display: flex;
                flex-direction: column;
                overflow: hidden;
                flex: 1;
                justify-content: space-evenly;
                @media (max-width: 850px) {
                    width: 0;
                }
                h3,
                p {
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    -o-text-overflow: ellipsis;
                }
            }

            .player_center_btn,
            .player_right_btn {
                display: flex;
                align-items: center;
                li {
                    width: 45px;
                    height: 45px;
                    line-height: 45px;
                    text-align: center;
                    font-size: 35px;
                    margin: 0 15px;
                    transition: 0.3s;
                    transform: scale(0.9);
                    &:hover {
                        background: rgba(200, 200, 200, 0.3);
                        border-radius: 50%;
                        opacity: 8;
                        box-shadow: inset 0 0 3px var(--el-color-primary);
                        color: var(--el-color-primary);
                        transform: scale(1);
                    }
                    &:active {
                        color: var(--el-color-primary-dark-2);
                        transform: scale(0.8);
                    }
                }
            }
            .player_center_btn li:nth-child(2) {
                width: 60px;
                height: 60px;
                line-height: 60px;
                font-size: 50px;
            }
            .player_right_btn li {
                margin: 0 5px;
            }
        }
        .player_right_bottom {
            display: flex;
            flex-direction: column;
            height: 20px;
            padding: 0 5px;
            margin-bottom: -5px;
        }
    }
}
</style>

<style>
.el-slider__button {
    width: 14px !important;
    height: 14px !important;
}
</style>