<template>
    <div>
        <el-menu :collapse="menuToggle" @select="handlerSelect">
            <img src="@/assets/Spectre_logo_blue.svg" />
            <div v-for="(item, index) in menus" :key="index">
                <el-tooltip :visible="visible[index]" placement="right" :disabled="!menuToggle">
                    <template #content>{{item.name}}</template>
                    <el-menu-item
                        :index="item.path"
                        @mouseenter="visible[index] = true"
                        @mouseleave="visible[index] = false"
                        :class="nowRouter == item.path ? 'router-active':''"
                    >
                        <i :class="item.meta.icon" style="font-size: 22px"></i>
                        <span>{{ item.name }}</span>
                    </el-menu-item>
                </el-tooltip>
            </div>
        </el-menu>
    </div>
</template>

<script setup>
import menus from "@/router/menu.js";
import { computed, ref, watch, onMounted } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

const visible = ref([]);

menus.forEach((item, index) => {
    visible.value[index] = false;
});

const store = useStore();
const router = useRouter();

const menuToggle = computed(() => {
    return store.state.menuToggle;
});
const nowRouter = ref("/");

const handlerSelect = val => {
    router.push(val);
};

onMounted(() => {
    window.onresize = () => {
        store.state.menuToggle = document.body.offsetWidth <= 950;
    };
});

watch(
    () => router.currentRoute.value.path,
    val => {
        nowRouter.value = val;
    },
    { immediate: true }
);
</script>

<style lang="less" scoped>
.el-menu {
    overflow: hidden;
    height: 100vh;
    border: none;
    user-select: none;
    @media (max-width: 950px) {
        height: 95vh !important;
    }
    img {
        width: 130px;
        padding: 12px 30px;
    }
    .el-menu-item {
        letter-spacing: 8px;
        text-indent: 5px;
        font-size: 16px;
        text-shadow: 1.5px 1.5px 1px #ccc;
        transition: 0.3s;
        &.is-active {
            color: #000;
        }
        &:hover {
            background: var(--el-color-primary-light-8);
        }
        &:active {
            background: var(--el-color-primary-light-7);
            transform: scale(0.9);
        }
        span {
            padding-left: 5px;
        }
        &.router-active {
            color: var(--el-color-primary);
            background: var(--el-color-primary-light-9);
            border-right: 6px solid var(--el-menu-active-color);
        }
    }
}
</style>
