import { createStore } from 'vuex'
import { useDark } from "@vueuse/core"
import Playlist from '@/data/playlist.json'

export default createStore({
    state: {
        webService: '/apis',
        menuToggle: false,
        playlistToggle: false,
        nowPlay: {
            "id": "",
            "song": "",
            "singer": "",
            "album": "",
            "pic": "",
            "mv": ""
        },
        nowLyric: "歌词加载中",
        nowIndex: 0,
        isDark: false,
    },
    getters: {
    },
    mutations: {
        setWebService: (state, url) => {
            state.webService = url
        },
        menuToggle: (state) => {
            state.menuToggle = !state.menuToggle;
        },
        playlistToggle: (state) => {
            state.playlistToggle = !state.playlistToggle;
        },
        useDark: (state, val) => {
            state.isDark = val;
            useDark().value = state.isDark;
        },
        setNowLyric: (state, val) => {
            state.nowLyric = val;
        },
        setNowIndex: (state, val) => {
            let index = val;
            if (val < 0) index = Playlist.length - 1;
            if (val > Playlist.length - 1) index = 0;
            state.nowIndex = index;
            state.nowPlay = Playlist[index];
        }
    },
    actions: {
    },
    modules: {

    }
})
