<template>
    <el-drawer
        v-model="drawer"
        size="300px"
        title="播放列表"
        :before-close="hanlder"
        custom-class="blur"
    >
        <ul class="playlist">
            <li v-for="(item, index) in Playlist" :key="index" @click="play(item, index)">
                <p>{{ item.song }}</p>
                <span>
                    {{ item.singer }}
                    <i v-if="item.mv">MV</i>
                </span>
            </li>
        </ul>
    </el-drawer>
</template>

<script setup>
import { computed, ref, watch } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";

import Playlist from "@/data/playlist.json";

const router = useRouter();
const store = useStore();

const drawer = ref(false);

const playlistToggle = computed(() => store.state.playlistToggle);

const hanlder = () => {
    store.commit("playlistToggle");
};

const play = (item, index) => {
    if (router.currentRoute.value.path.includes("mv")) {
        if (item.mv) {
            router.push({ path: "/mv", query: { id: item.id, mv: item.mv } });
            return false;
        } else {
            router.push({ path: "/" });
        }
    }
    store.commit("setNowIndex", index);
};

watch(playlistToggle, val => (drawer.value = val));
</script>

<style lang="less">
.el-drawer {
    position: fixed !important;
    top: 20px !important;
    right: 20px !important;
    bottom: 20px !important;
    border-radius: 10px !important;
    height: auto !important;
    box-shadow: 0 0 15px #aaa !important;
}
.el-overlay {
    background: none !important;
}
.el-drawer__header {
    margin-bottom: 0 !important;
}
.playlist {
    li {
        margin: 0 -8px;
        padding: 8px 10px;
        transition: 0.3s;
        cursor: pointer;
        border-top: 1px solid rgba(130, 130, 130, 0.2);
        &:hover {
            border-radius: 5px;
            background: rgba(240, 240, 240, 0.3);
            transform: scale(1.03);
            box-shadow: 0 0 5px gray;
        }
        &:active {
            background: rgba(200, 200, 200, 0.4);
            transform: scale(1.02);
            color: var(--el-color-primary);
        }
        p {
            font-size: 15px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            -o-text-overflow: ellipsis;
        }
        span {
            margin-top: 3px;
            display: block;
            color: #777;
            font-size: 12px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            -o-text-overflow: ellipsis;
            display: flex;
            i {
                font-style: normal;
                color: #409eff;
                background: rgba(236, 245, 255, 0.5);
                padding: 0 5px;
                border: 1px solid #409eff;
                border-radius: 3px;
                transform: scale(0.8);
                margin-left: 3px;
            }
        }
    }
}
</style>
